@import url(https://fonts.googleapis.com/css?family=Poppins);
svg text{
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.Geography {
    stroke-width: 0.7;
}
.UsMapQuiz {
    display: flex;
    width: 100%;
}

@media screen and (orientation:portrait) {
    .UsMapQuiz {
        flex-direction: column;
    }
}

@media screen and (orientation:landscape) {
    .UsMapQuiz {
        flex-direction: row;
    }

    .UsMap {
        top: -6vh;
    }

    .UsMapStats {
        max-width: 40vw;
    }
 }

.UsMapStats {
    display: flex;
    flex-direction: column;
    text-align: center;
}

.UsMapStatsHeader > * {
    margin: 0;
}

.UsMapStatsInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.UsaFlagImg {
    width: 20vw;
}

.RestartQuizButton {
    margin: 5px;
    padding: 5px;
    cursor: pointer;
    border: 1px black solid;
}

.UsMap {
    position: relative;
    width: 100%;
}

.QuizCurrentState {
    padding: 3px;
    border-width: 4px 0 4px 0;
    border-image: linear-gradient(to right, #002C77,#CC0C2F);
    border-image-slice: 1;
    border-style: solid;
    margin: 0;
}

.Unselectable {
    -webkit-user-select: none; /* Safari */ /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
}
html {
    background-color: #b1d6f1;
}

.App {
    font-family: 'Poppins', cursive;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Unselectable {
    -webkit-user-select: none; /* Safari */ /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
}

